import Vue from 'vue'
import Router from 'vue-router'
import VueResource from 'vue-resource'
import Login from '@/components/Login'
import Home from '@/components/Student/Home'
import Home2 from '@/components/Student/Home2'
import Resume from '@/components/Student/Resume'
import Search from '@/components/Student/Search'
import Search2 from '@/components/Student/Search2'
import MyJob from '@/components/Student/MyJob'
import MyTalk from '@/components/Student/MyTalk'
import JobDetail from '@/components/Student/JobDetail'
import MyJobDetail from '@/components/Student/MyJobDetail'
import TalkDetail from '@/components/Student/TalkDetail'
import MyTalkDetail from '@/components/Student/MyTalkDetail'

import ResSer from '@/components/User/ResSer'
import Enterprice from '@/components/User/Enterprice'
import ManPos from '@/components/User/ManPos'
import ManTalk from '@/components/User/ManTalk'
import NewPos from '@/components/User/NewPos'
import NewTalk from '@/components/User/NewTalk'

Vue.use(Router)
Vue.use(VueResource)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/Student/Home',
      name: 'Home',
      component: Home
    },
    {
      path: '/Student/Home2',
      name: 'Home2',
      component: Home2
    },
    {
      path: '/Student/Resume',
      name: 'Resume',
      component: Resume
    },
    {
      path: '/Student/Search',
      name: 'Search',
      component: Search
    },
    {
      path: '/Student/Search2',
      name: 'Search2',
      component: Search2
    },
    {
      path: '/Student/MyJob',
      name: 'MyJob',
      component: MyJob
    },
    {
      path: '/Student/MyTalk',
      name: 'MyTalk',
      component: MyTalk
    },
    {
      path: '/Student/Resume',
      name: 'Resume',
      component: Resume
    },
    {
      path: '/Student/JobDetail',
      name: 'JobDetail',
      component: JobDetail
    },
    {
      path: '/Student/MyJobDetail',
      name: 'MyJobDetail',
      component: MyJobDetail
    },
    {
      path: '/Student/TalkDetail',
      name: 'TalkDetail',
      component: TalkDetail
    },
    {
      path: '/Student/MyTalkDetail',
      name: 'MyTalkDetail',
      component: MyTalkDetail
    },
    {
      path: '/User/ResSer',
      name: 'ResSer',
      component: ResSer
    },
    {
      path: '/User/Enterprice',
      name: 'Enterprice',
      component: Enterprice
    },
    {
      path: '/User/NewPos',
      name: 'NewPos',
      component: NewPos
    },
    {
      path: '/User/NewTalk',
      name: 'NewTalk',
      component: NewTalk
    },
    {
      path: '/User/ManPos',
      name: 'ManPos',
      component: ManPos
    },
    {
      path: '/User/ManTalk',
      name: 'ManTalk',
      component: ManTalk
    }
  ]
})



